	module.exports = {"data":{"markdownRemark":{"html":"<h2>Introduction</h2>\n<p>I am the markdown file</p>\n<p>All teams designing and developing products desire a better understanding of the users that they’re ultimately creating the product to serve. With the multitude of options available for learning about a particular set of users, determining the methods that should be employed to gain that understanding can be challenging.\nTeams are also interested in maximizing the value of their time, and time spent on user research could alternatively be spent on designing or building instead. The following guidelines provide context for some of the most commonly used user research methods employed by design teams and user experience professionals around the world. The goal of this is to convince the reader — or help the reader convince their stakeholders — that the cost of conducting user research far outweighs the potential costs of not doing so.</p>\n<h2>Answers to common questions</h2>\n<h3>What do you mean by user experience?</h3>\n<p>“User experience” (often abbreviated as uX) refers to the impressions and emotions created while a person interacts with a system. In this context, a “system” typically refers to a website or software application, although the principles of uX can be extrapolated to interaction with nearly any digital or physical product.</p>\n<h3>What do you mean by discovery?</h3>\n<p>“Discovery” is an umbrella term that includes essentially any research methods or tactics that focus on understanding user behaviors, needs, preferences, or motivations through direct observation, surveying, or feedback gathering, including the documentation and analysis of results.</p>\n<p>Discovery activities most often take place at the beginning of a project (sometimes referred to as the “discovery Phase”), but they can be conducted any time during a project’s lifecycle.</p>\n<h3>What are the typical roles within UX teams?</h3>\n<p>No two teams are truly identical, and many UX teams operate with more/fewer/di erent roles. The most common roles you’ll find within a typical UX team include: ...</p>","frontmatter":{"title":"User Research Guidelines","overview":null,"resources":null,"relatedMethods":null},"fields":{"slug":"/user-research-guidelines/"}},"allMarkdownRemark":{"edges":[{"node":{"frontmatter":{"title":"A/B Test","url":"/ab-test/"}}},{"node":{"frontmatter":{"title":"Affinity Diagram","url":"/affinity-diagram/"}}},{"node":{"frontmatter":{"title":"Card Sort","url":"/card-sort/"}}},{"node":{"frontmatter":{"title":"Empathy Map","url":"/empathy-map/"}}},{"node":{"frontmatter":{"title":"Focus Group","url":"/focus-group/"}}},{"node":{"frontmatter":{"title":"Interview","url":"/interview/"}}},{"node":{"frontmatter":{"title":"Journey Map","url":"/journey-map/"}}},{"node":{"frontmatter":{"title":"Participatory Design","url":"/participatory-design/"}}},{"node":{"frontmatter":{"title":"Persona","url":"/persona/"}}},{"node":{"frontmatter":{"title":"Usability Testing","url":"/usability-testing/"}}},{"node":{"frontmatter":{"title":"User Research Guidelines","url":null}}}]}},"pathContext":{}}

